.project>img{
    width: 400px;
    height: 300px;
    border: 10px solid rgb(0, 0, 0);
    transition: all 0.3s ease-in;

}

.projectContainer{
    padding-top: 10px;
}

.project>img:hover{
    transform: scale(110%);
    box-shadow: -10px 10px 30px  rgb(39, 57, 255);
}



.projects{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 50px 50px;
    justify-content: center;
}

@media screen and (max-width: 600px)
{
    .project>img{
        width: 350px;
        height: 270px;
        border: 10px solid rgb(0, 0, 0);
        transition: all 0.3s ease-in;
    
    }
}